.body {
    margin: 0;
    padding: 0;
    font-family: 'Times New Roman', Times, serif;
}
.container{
    display: flex;
    
}
.wraper {
    background-color: #333;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 15px;
    width: 100%;
    height: 90px;
    box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0, 5);
    border-bottom: solid 3px white;
}
.profile{
    display: flex;
    flex-direction: row;
    font-size: 1.5em;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.one{
    color: white;
    font-weight: 700;
    margin-left: 40px;
}
.xixi{
    margin-right: 20px;
}

.xixi .warpel{
    display: flex;
}
.warpel li{
    list-style: none;
    padding: 15px;
}
.warpel li a {
    color: white;
    font-size: 20px;
    text-decoration: none;
    text-transform: capitalize;
    /* margin-left: 180px; */
}
.warpel #active{
    background-color: crimson;
    color: white;
    padding: 15px;
    border-radius: 15px;
}
.warpel li a:hover{
    color: grey;
    transition: all .0,3s ease-in-out;
}
