body {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  .containerFot {
    background: #333;
    height: auto;
    /* width: 100vh;  */
    padding-top: 40px;
    font-family: Arial, Helvetica, sans-serif;
    color: white;
  }
  .footer-content {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    text-align: center;
    box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0, 5);
    border-bottom: solid 3px white;
  }
  .footer-content h3 {
    font-size: 1.6rem;
    font-weight: 500;
    text-transform: capitalize;
    line-height: 3rem;
  }
  .footer-content p {
    max-width: 500px;
    margin: 10px auto;
    line-height: 28px;
    font-size: 14px;
  }
  .social {
    list-style: none;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 1rem 0 3rem 0;
  }
  .social li {
    margin: 0 10px;
  }
  .social a {
    text-decoration: none;
    color: white;
  }
  
  .social li a {
    font-size: 1.1rem;
    transition: color 0.4s ease;
    justify-content: center;
    text-align: center;
    margin-right: 10px;
  }
  .social li:hover {
    color: crimson;
  }
  .social li #tt:hover {
    color: black;
  }
  .social li #ig:hover {
    color: rgb(253, 57, 90);
  }
  .footer-btn {
    background: #333;
    /* width: 100vh; */
    padding: 20px 0;
    text-align: center;
  }
  .footer-btn p {
    font-size: 14px;
    word-spacing: 2px;
    /* text-transform: capitalize; */
    color: white;
  }
  .footer-btn span {
    text-transform: uppercase;
    opacity: 0.4;
